import React from 'react'
import { Link } from 'react-router-dom'

const Submenu = props =>{
  const submenu= props.text
  return(
    <div className="hover">
      <ol className="submenu">
        {submenu.map(item=>{
            return(
              <li key={item.txt}> <Link to='/'>{item.txt}</Link> </li>
            )
        })}
      </ol>
    </div>
  )
}

export default Submenu