const DoLogin = props => {
  const username = props.username
  const passwd = props.passwd
  const vpic = props.vpic
  const dispatch = props.dispatch
  const data = {
    username: username,
    password: passwd,
  }
  // console.log(vpic)
  if (username === '' || username === '帐户') {
    alert('账号请务必输入！')
    return false
  }
  if (passwd === '' || passwd === '******') {
    alert('密码请务必输入！')
    return false
  }
  if (vpic === '' || vpic === '验证码' || vpic.length < 4) {
    alert('验证码请务必输入！')
    return false
  }
  if (username === data.userName && passwd === data.passWord && vpic === data.vPic) {
    alert('登入成功')
  } else {
    const vpicNum = Promise.resolve(logincheck(vpic, 'login'))
    vpicNum.then(function(result) {
      // console.log(result);
      if (result === 0) {
        alert('验证码错误，请重新输入')
      } else if (result === 1) {
        const loginNum = Promise.resolve(axiosPost(data))
        loginNum.then(function(result) {
          // console.log(result);
          if (result === undefined) return alert('伺服器錯誤')
          if (result === 0) {
            alert('用户名或密码错误，请重新输入')
          } else {
            alert('登陆成功')
            dispatch(changeNum(1))
            dispatch(checkLogin(result))
            sessionStorage.setItem('sessionNum', 1)
            sessionStorage.setItem('sessionNmae', result)
            // console.log(result)
          }
        })
      } else {
        alert('伺服器錯誤')
      }
    })
  }
}
const Pw_forget = () => {
  alert('忘记账号密码，请联系在线客服人员取回！')
}
export default Login
