import React, { useState, createContext, useContext } from 'react';
import { Link } from 'react-router-dom'
import { useSelector,useDispatch  } from 'react-redux'
import { checkLogin } from '../action/login'
import  axiosGet  from '../axios/index'
import Login from '../component/login'
import Banner from '../component/banner'

const Home = props =>{
  return (
    <div className='index'>
      <div className='index-banner'>
        <Login />
        <Banner />
      </div>
      <div className='index-main'>
        <News />
        <div className='i-m-r'>
          <IndexJoin />
          <GameList />
        </div>
      </div>
    </div>
  )
}


const News = props =>{
    return(
      <div className="i-m-l">
			<div className="first-news">
			
			</div>
			<div className="web_info">
            客戶：vnso767@gmail.com
        <br/>推廣：vnso767@gmail.com
        <br/>投訴：o767vns@gmail.com

			</div>
		</div>
    )
}
const IndexJoin = props =>{
  return(
    <div className='first-pic'>
      <div className='join_now'>
        <Link to='/'></Link>
      </div>
    </div>
  )
}
const GameList = props =>{
  // const live =[
  //   {text:'百家乐'},
  //   {text:'骰宝'},
  //   {text:'二八杠'},
  //   {text:'龙虎斗'},
  //   {text:'轮盘'},
  //   {text:'金臂百家乐'}
  // ]
  // const sport =[
  //   {text:'足球赛事'},
  //   {text:'时时滚球'},
  //   {text:'NBA职蓝'},
  //   {text:'美式足球'},
  //   {text:'冰球'},
  //   {text:'各式冠军赛事'}
  // ]
  // const dianzi =[
  //   {text:'老虎机'},
  //   {text:'电动扑克'},
  //   {text:'大型电玩'},
  //   {text:'桌上游戏'},
  //   {text:'经典老虎机'}
  // ]
  // const data = [live, sport, dianzi ]
  const data =  useSelector(state =>state.gameReducer.gameList)
  return(
    <div className='gamelist'>
      {data.map((item,index) =>{
        return (
          <div className='indexgame' key={`item${index}`}>
            <Link to='/' className='game_btn'></Link>
            <GameFont txt={item}/>
          </div>
        )
      })}
    </div>
  )
}
const GameFont = props =>{
  const { txt } = props
  return (
      <div className='game-font'>
          {txt.map((item,index) =>{
          return  <Link key={item.text} to='/'>{item.text}</Link>
          })} 
    </div>
  )
}


export default Home