import axios from 'axios'
import qs from 'qs'
axios.defaults.withCredentials = true

//GET
const axiosGet = () => {
  const url = 'https://api.kcg.gov.tw/api/service/Get/f2c5f476-3f1b-4904-acd4-c360cbfb8773'
  axios
    .get(url)
    .then(res => {
      console.log(res)
    })
    .catch(err => {
      console.log(err)
    })
}

export { axiosGet }

const nodePost = props => {
  const data = {
    name:'tom',
    tel:'123456789'
  }
  console.log(data)
  const url = 'http://127.0.0.1:8888/test'
  //配置`transformRequest` ，在向伺服器傳送前，修改請求資料，axios會根據修改後的資料，自動設定請求頭
  return axios
    .post(url, data, {
      transformRequest: [
        data => {
          return qs.stringify(data) //把資料轉化為QueryString
        },
      ],
    })
    .then(res => {
      console.log(res)
      // console.log(res.data)
      return res.data
    })
    .catch(err => {
      console.log(err)
      // return err
    })
}
export { nodePost }

