import React from 'react'
import { Link, Switch, Route } from 'react-router-dom'
import OtherTop from '../component/otherTop'
import AboutIndex from './about/aboutIndex'
import Join from './about/join'
import Novice from './about/novice'
import Agent from './about/agent'
import Partner from './about/partner'
import Deposit from './about/deposit'
import Withdraw from './about/withdraw'
import ContactUs from './about/contactUs'

const About = props => {
  const imgs = {
    img1: `gamewlecome_tit.jpg`,
    img2: `topic_wlecome.jpg`,
  }
  return (
    <div className='index sxzb'>
      <OtherTop imgs={imgs} />
      <div className='ljjjr-main'>
        <AboutLeft />
        <AboutRight />
      </div>
    </div>
  )
}

const AboutLeft = props => {
  const data = [
    { text: '免费开户', url: '/about/Join' },
    { text: '关于我们', url: '/about' },
    { text: '新手上路', url: '/about/novice' },
    { text: '代理合作', url: '/about/agent' },
    { text: '合作加盟', url: '/about/partner' },
    { text: '存款帮助', url: '/about/deposit' },
    { text: '取款帮助', url: '/about/withdraw' },
    { text: '联系我们', url: '/about/contactUs' },
  ]
  return (
    <div className='ljjjr-left'>
      <h3>
        <img src={require(`../static/images/abTitle.jpg`)} alt='圖片'></img>
      </h3>
      <ul>
        {data.map(item => (
          <li key={item.text}>
            <Link to={item.url}>{item.text}</Link>
          </li>
        ))}
      </ul>
    </div>
  )
}

const AboutRight = props => {
  return (
    <div className='ljjjr-right'>
      <Switch>
        <Route exact path='/about' component={AboutIndex} />
        <Route path='/about/join' component={Join} />
        <Route path='/about/novice' component={Novice} />
        <Route path='/about/agent' component={Agent} />
        <Route path='/about/partner' component={Partner} />
        <Route path='/about/deposit' component={Deposit} />
        <Route path='/about/withdraw' component={Withdraw} />
        <Route path='/about/contactUs' component={ContactUs} />
      </Switch>
    </div>
  )
}

export default About
