import React, {useState} from 'react';
import { Link } from 'react-router-dom'
import OtherTop from '../component/otherTop'

const Hot = props =>{
  const [click,setClick] = useState('none')
  const imgs={
    img1:`yhhd-top.jpg`,
    img2:`yhhdbt.jpg`
  }
  return(
    <div className='index sxzb'>
      <OtherTop imgs={imgs}/>
      <div className='yhhhy-m'>
        <ul className='jiaru2_zc'>
            <li onClick={()=>{
                click ==='block' ? setClick('none') : setClick('block')
              }}>
              <span className='hd-img'>
                <img src={require('../static/images/157521822015752031811130.png')}  alt='圖片'/> 
              </span>
              <Link to='/' style={{display:click}}>
                <ImgTxt click={click}/>
              </Link>
            </li>
        </ul>
      </div>
    </div>
  )
}

const ImgTxt = props =>{
  const style ={
    background:'#ff0',
    textAlign: 'center',
    fontSize:'25px'
  }
  return(
    <div style={style}>
      <p>測試</p>
      <p>測試</p>
      <p>測試</p>
      <p>測試</p>
      <p>測試</p>
      <p>測試</p>
    </div>
  )
}
export default Hot