import React from 'react'
import { Link } from 'react-router-dom'

const Footer = props => {
  return (
    <div className='footer'>
      <div className='footWarp'>
        <Link to='/' className='hyllq'>
          <img src={require('../static/images/ublogo.png')} alt='圖片' />
        </Link>
        <div className='footMenu'>
          <ul>
            <li>
              <Link to='/about'>关于我们</Link> |
            </li>
            <li>
              <Link to='/about/contactUs'>联系我们</Link> |
            </li>
            <li>
              <Link to='/about/agent'>代理合作</Link> |
            </li>
            <li>
              <Link to='/about/deposit'>存款帮助</Link> |
            </li>
            <li>
              <Link to='/about/withdraw'>取款帮助</Link> |
            </li>
            <li>
              <Link to='/about/partner'>合作加盟</Link> |
            </li>
            <li>
              <Link to='/about/novice'>新手上路</Link> 
            </li>
            {/* <li>
              <Link to='/about'>线路检测</Link>
            </li> */}
          </ul>
          <div className='copy'>Copyright © 威尼斯人娱乐城 Reserved</div>
        </div>
      </div>
    </div>
  )
}

export default Footer
