import React from 'react'
import {Link} from 'react-router-dom'
import {changeNum} from '../action/login'
const Chk_rule = props =>{
  const dispatch = props.path
  return(
    <div className='chk_rule'>
      <div className='content'>
        <div className="logo"></div>
        <div className ='rulesBody'>
          <div className="rulesTile">协议与规则</div>
          <div className="formDIV">
            <input name="submit" type="button" className="za_button" value="我不同意" onClick={()=>{
              dispatch(changeNum(0)) 
              sessionStorage.removeItem('sessionNum');
              sessionStorage.removeItem('sessionNmae');
              }} tabIndex="2"></input>
            <input name="submit" type="button" className="za_button" value="我同意" onClick={()=>{
              dispatch(changeNum(2))
              sessionStorage.setItem('sessionNum', 2);
              }} tabIndex="1" ></input>
          </div>
          <table border="0" align="center" cellPadding="0" cellSpacing="0">
            <tbody><tr>
                <td width="100" className="title-style3"><div align="center"><Link to='/'>通则</Link></div></td>
                <td width="100" className="title-style3"><div align="center" id="rule_ipl_link"><Link to='/'>线上娱乐城</Link></div></td>
                <td width="100" height="30" className="title-style3"><div align="center"><Link to='/'>运动投注</Link></div></td>
                <td width="100" height="30" className="title-style3"><div align="center"><Link to='/'>其他游戏</Link></div></td>
              </tr>
            </tbody>
          </table>
          <RuleBody />
        </div>
      </div>
    </div>
  )
}
const RuleBody = props =>{
  return(
    <div id="rule_all" >
			<table width="655" border="0" align="center" cellPadding="0" cellSpacing="0">
				<tbody>
          <tr>
            <td height="30" className="title-style2">
              <div align="center"><p align="center">通则</p></div>
            </td>
          </tr>
          <tr>
            <td>
              <ol>
                <li className="title-style1">为避免于本网站投注时之争议，请会员务必于进入网站前详阅本娱乐城所定之各项规则，客户一经“我同意”进入本网站进行投注时，即被视为已接受本娱乐城的所有协议与规则。</li>
                <li className="title-style1">会员有责任确保自己的帐户以及登入资料的保密性，以会员帐号及密码进行的任何网上投注，将被视为有效。敬请不定时做密码变更之动作。若帐号密码被盗用，进行的投注，本公司一概不负赔偿责任。</li>
                <li className="title-style1">本公司保留不定时更改本协定或游戏规则或保密条例，更改之条款将从更改发生后指定之日起生效，并保留一切有争议事项及最后的决策权。</li>
                <li className="title-style1">用户须达到居住地国家法律规定之合法年龄方可使用线上娱乐场或网站。</li>
                <li className="title-style1">网上投注如未能成功提交，投注将被视为无效。</li>
                <li className="title-style1">凡玩家于出牌途中且尚无结果前自动或强制断线时，并不影响比赛之结果。</li>
                <li className="title-style1">如遇发生不可抗拒之灾害，骇客入侵，网络问题造成数据丢失的情况，以本公司公告为最终方案。</li>
                <li className="title-style1">特此声明，本公司将会对所有的电子交易进行记录，如有任何争议，本公司将会以注单记录为准。</li>
                <li className="title-style1">本公司保留更改、修改现有条款或增加任何适当条款的权利，而条款改动后将会在会员端跑马灯上公布。</li>
                <li className="title-style1">无论在任何情况下，本公司具有最终的解释权。开始新局。</li>
                <li className="title-style1">若经本公司发现会员以不正当手法&lt;利用外挂程式&gt;进行投注或以任何非正常方式进行的个人、团体投注有损公司利益之投注情事发生，本公司保留权利取消该类注单以及注单产生之红利，并停用该会员帐号。</li>
                <li className="title-style1">若本公司发现会员有重复申请帐号行为时，保留取消、收回会员所有优惠红利，以及优惠红利所产生的盈利之权利。每位玩家、每一住址、每一电子邮箱、每一电话号码、相同支付卡/信用卡号码，以及共享电脑环境 (例如:网吧、其他公共用电脑等)只能够拥有一个会员帐号，各项优惠只适用于每位客户在本公司唯一的帐户。</li>
                <li className="title-style1">本娱乐城拥有对所有游戏规则与条款的修改权和最终解释权。</li>
              </ol>    
            </td>
          </tr>
          <tr>
            <td height="30"><div align="right"><Link to='/' className="title-style3">TOP</Link></div></td>
          </tr>
        </tbody>
      </table>
		</div>
  )
}
export default Chk_rule