import * as actions from '../action/login';

const loginName = ''
const initState ={
  loginName
} 

const loginReducer = (state = initState, action) =>{
  // console.log(action)
  // console.log(state)
  switch (action.type){
    case actions.LOGIN:
      return action.payload 
    default:
      return state
  }
}
// action.payload.username
export default loginReducer