import React from 'react';
import Login from '../component/login'

const OtherTop = props =>{
  const {imgs} =props
  const style ={
    height:'302px',
    background:'none'
  }

  return(
    <>
      <div className='index-banner'style={style}>
        <Login bottom={true}/>
        <div className='index-b-r' style={style}>
          <img src={require(`../static/images/${imgs.img1}`)}  alt='圖片'/> 
        </div>
      </div>
      <div className='game_topic'>
        <img src={require(`../static/images/${imgs.img2}`)}  alt='圖片'/> 
      </div>
    </>
  )
}

export default OtherTop