import React from 'react'
// import logo from './logo.svg';
import './App.scss'
import { Switch, Route } from 'react-router-dom'
import Header from './component/header.js'
import Footer from './component/Footer'
import Home from './page/home'
import Sport from './page/sport'
import Live from './page/live'
import Dianzi from './page/dianzi'
import Qipai from './page/qipai'
import Lottery from './page/lottery'
import Six from './page/six'
import Hot from './page/hot'
import About from './page/about'
import Chk_rule from './page/chk_rule'

function App() {
  return (
    <div className='App'>

       <Header />
        <Switch>
          <Route exact path='/' component={Home} />
          <Route path='/sport/' component={Sport} />
          <Route path='/live/' component={Live} />
          <Route path='/dianzi/' component={Dianzi} />
          <Route path='/qipai/' component={Qipai} />
          <Route path='/lottery/' component={Lottery} />
          <Route path='/six/' component={Six} />
          <Route path='/hot/' component={Hot} />
          <Route path='/about/' component={About} />
          <Route path='/chk_rule/' component={Chk_rule} />
        </Switch>
        <Footer />

    </div>
  )
}

export default App
